<template>
    <v-main>
        

        <v-row class="fill-width pa-3" align="center" justify="center">
            <v-card width="1000px">

                <v-app-bar color="green darken-2" width="auto" height="40px">
                <v-spacer/>HOME<v-spacer/>    
                </v-app-bar>

                <v-progress-linear indeterminate v-if="loading" />

                <v-row sm class="pa-3" align="center" justify="center" v-if="!loading">
                    
                    <v-col cols="12" v-for="(mess, i) in news" :key="i">
                        <v-card height="auto" color="blue darken-4">
                            <v-app-bar height="35px">
                                <v-spacer/> {{mess.title}} <v-spacer/>
                            </v-app-bar>
                            <v-img :src="mess.src" max-height="150px"/><hr>
                            <v-card-text v-html="mess.text">
                                {{mess.text}}
                            </v-card-text>
                        </v-card>
                    </v-col>
                </v-row>
                <v-pagination class="my-4" v-model="page" :length="(news_n + 9) / 9" />
            </v-card>

        </v-row>
        

    </v-main>
</template>

<script>
import axios from 'axios'

export default {
    data() {
        return {
            news: {},
            news_n: 0,
            page: 1,
            loading: ''
        }
    },

    mounted() {
        this.loading = true
        axios.get('./jsons/news.json')
        .then(resp => {
            this.news = resp.data
            console.log(this.news.length)
            this.loading = false

            for (let i in resp.data) {
                this.news_n++
                console.log(i)
            }
        })
    }

}
</script>

